* {
    font-family: "Roboto", sans-serif; }

nav {
    height: 50px;
    background-color: #212121;
    position: fixed;
    width: 100%;
    z-index: 100; }

header {
     position: relative; }

.menu {
    display: flex;
    justify-content: space-between;
    padding: 13px 70px;
    margin: 0;
    list-style-type: none;

    .memu__link {
        color: #ffffff;
        font-size: 15px;
        font-weight: 300;
        text-decoration: none; }

    .menu__item:not(:last-child) {
        border-right: 2px dotted white;
        padding-right: 50px; } }

.subheader {
    position: absolute;
    width: 100%;
    height: 84px;
    background-color: rgba(0, 0, 0, 0.6);
    padding-top: 16px;
    margin-top: 50px;
    z-index: 50;

    .subheader__logo {
        display: block;
        max-width: 100%;
        margin-bottom: 4px; }

    .subheader__text {
        color: #ffffff;
        font-size: 13px;
        font-weight: 400;
        opacity: 0.5; }

    .subheader__call {
        color: #ffffff;
        font-size: 12px;
        font-weight: 300;
        text-align: right; }

    .subheader__phone {
        display: block;
        color: #ffffff;
        text-transform: uppercase;
        font-size: 22px;
        font-weight: 400;
        text-decoration: none;
        cursor: pointer;
        text-align: right; }

    .subheader__btn {
        width: 172px;
        height: 46px;
        box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 14px;
        font-weight: 400;
        text-transform: uppercase; } }

.promo {
    position: relative;
    min-height: 800px;
    background: url(../img/promo-bg.png) (center / cover) no-repeat;
    padding: 150px 0 135px;

    // transform: translateY(-84px)
    //     z-index: -1
    top: 50px;

    .promo__header {
        color: #fff;
        font-size: 60px;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
        line-height: 60px;
        margin: 0;
        margin-bottom: 22px; }

    .promo__subheader {
        color: #fff;
        font-size: 36px;
        font-weight: 400;
        text-transform: uppercase;
        text-align: center;
        line-height: 36px;
        margin: 0;
        margin-bottom: 23px; }

    .promo__descr p {
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-align: center;
        line-height: 24px;

        &:last-child {}
        margin-bottom: 118px; }

    .promo__btn {
        display: block;
        width: 259px;
        height: 63px;
        box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
        border-radius: 4px;
        background-image: linear-gradient(to top, #1eacc7 0%, #4ce2ff 100%);
        text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
        color: #ffffff;
        font-size: 18px;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;
        margin: 0 auto; } }

.advantages {
    min-height: 764px;
    margin-top: 50px;
    padding-top: 71px;

    .subtitle__box--black {
        max-width: min-content;
        height: 24px;
        background-color: #000000;
        font-size: 11px;
        font-weight: 400;
        text-align: center;
        text-transform: uppercase;
        margin: 0 auto;
        padding-top: 4px;
        color: #ffffff; }

    .advantages__subtitle {
        margin-bottom: 44px; }



    .advantages__items-wrapper {
        display: flex;
        min-height: 250px; }

    .advantages__item-text {
        margin-left: 26px;
        width: 341px; }

    .advantages__logo {
        position: relative;
        width: 113px;
        height: 113px;
        background-color: #1eacc7;
        border-radius: 50%;
        top: 15px; }

    .advantages__icons {
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); }

    .advantages__item-title {
        color: #1eacc7;
        font-size: 17px;
        font-weight: 400; }

    .advantages__subtitle-paragraph {
        font-size: 15px;
        font-weight: 300;
        line-height: 21px;
        text-align: justify; }

    .title__h2--black {
        font-size: 38px;
        font-weight: 700;
        line-height: 48px;
        text-align: center;
        text-transform: uppercase;
        margin-bottom: 40px;
        color: #222222; } }



.subtitle__box--black {
    max-width: min-content;
    height: 24px;
    background-color: #000000;
    font-size: 11px;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
    margin: 0 auto; }

.title__h2--black {
    font-size: 38px;
    font-weight: 700;
    line-height: 48px;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 40px;
    color: #222222; }


.mobile {
    min-height: 864px;
    padding: 48px 0 79px;
    background: url(../img/mobile-section-bg.png) center / cover no-repeat;
    &__item {
        width: 320px;
        height: 280px;
        margin: 0 auto;
        &--1 {
            background: url(../img/mobile-1.png) center / cover no-repeat; }
        &--2 {
            background: url(../img/mobile-2.png) center / cover no-repeat; }
        &--3 {
            background: url(../img/mobile-3.png) center / cover no-repeat; }
        &--4 {
            background: url(../img/mobile-4.png) center / cover no-repeat; }
        &--5 {
            background: url(../img/mobile-5.png) center / cover no-repeat; }
        &--6 {
            backgroung: url(../img/mobile-6.png) center / cover no-repeat; } } }


.label {
    color: #ffffff;
    padding-top: 4px;
    margin-bottom: 46px; }


.subtitle {
    &__small {
        font-size: 17px;
        font-weight: 400;
        line-height: 21px;
        text-align: center;
        &--black {
            color: black; } } }
